body, html {
  /* margin: 190; */

  padding: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f0f0f0;
  color: #333;
}

.about {
  padding: 40px;
  max-width: 1300px;
  margin: 10px auto;
  /* margin-top: 10px; */
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.about-header h2 {
  color: #ff3366;
  font-size: 2.5rem;
  font-weight: 700;
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-bottom: 20px;
}

.about-image {
  flex: 1;
  margin-top: 20px;
  margin-right: 20px;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about-text {
  flex: 2;
  padding-left: 20px;
  margin-top: 20px;
}

.about-text p {
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
  text-align: justify;
}

.quote {
  margin-top: 30px;
  padding: 20px;
  background-color: #f7f7f7;
  border-left: 5px solid #ff3366;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.quote p {
  font-size: 1.2rem;
  font-style: italic;
  color: #333;
  margin: 0;
}

@media only screen and (max-width: 768px) {
  .about-content {
    flex-direction: column;
  }

  .about-image {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .about-text {
    padding-left: 0;
  }
}
